<template>
    <div>
        <article>
            <main id="mdHtml"></main>
        </article>
    </div>
</template>

<script>
export default {
    name:"ArticleDetails",
    async mounted(){
        const id = this.$route.params.id;  //得到动态路由的id值
        let {data} = await this.$axios({
            method:"GET",
            url:"/get/articleDetails",
            params:{id}
        });
        //发起请求，通过md文件地址得到文件数据
        // console.log(data.data.mdUrl);
        let mdData = await this.$axios({
            method:"GET",
            url: data.data.mdUrl,
        });
        // console.log(mdData);
        editormd.markdownToHTML("mdHtml", {
            markdown: mdData.data //md文件数据
        });
    }
}
</script>
<style lang="less" scoped>
    #mdHtml{
        background-color: white;
        border-radius: 10px;
        min-height: 630px;
    }
</style>